class Tavolo extends React.Component {

  constructor(props) {
    super(props);
    //0=home, 1=thewall and so on
    this.state = {
                    page: this.props.page,
                    data: [],
                 };
    this.componentDidMount = this.componentDidMount.bind(this);
  }

  render() {
    const { data } = this.state;
    return(
      <TableContainer component={Paper} className="content">
      <div style={{minHeight: 64}} />
      <Table className="table" aria-label="simple table">
        <TableHead>
          <TableRow className="tablehead">
            <TableCell><b>Username</b></TableCell>
            <TableCell align="left"><b>Date</b></TableCell>
            <TableCell align="left"><b>Time</b></TableCell>
            {(this.state.page == 0) && <TableCell align="left"><b>Command</b></TableCell>}
            {/*<TableCell align="right"><b>Balloon ID#</b></TableCell>*/}
          </TableRow>
        </TableHead>

        <TableBody>
        {console.log('data:', data, typeof data)}
        {data.map((data) => (
            <TableRow key={data.uid}>
              <TableCell alighn="left">{data.username}</TableCell> {/*component="th"  scope="data"*/}
              <TableCell align="left">{data.date}</TableCell>
              <TableCell align="left">{data.time}</TableCell>
              {this.state.page == 0 && <TableCell align="left">{"Tilt " + data.orientation}</TableCell>}
             
            </TableRow>
          ))}

        </TableBody>
      </Table>
      </TableContainer>
    );
  }

  async componentDidMount() {
    if (this.state.page == 0) {
      let response = await axios.get('http://120.78.126.174:3000/history');
      this.setState({data: await response.data})
      console.log('didmount', typeof this.state.data)
    } else if (this.state.page == 1) {
      let response = await axios.get('http://120.78.126.174:3000/queue');
      this.setState({data: await response.data})
      console.log('didmount', typeof this.state.data)
    }
  }
}

