function Maine() {
  const classes = useStyles();
  var username = randomName();// "Anonymous";

  const [status, setStatus] = React.useState(null);
  const [curUsername, setUsername] = React.useState(null);
  const [updateTime, setUpdateTime] = React.useState(null);
  const [queuelen, setQueueLen] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [popMessage, setPopMessage] = React.useState("");

  const handleClick = (message) => {
    console.log('handleClick is called');
    setOpen(true);
    setPopMessage(message);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      //return;
    }
    setOpen(false);
  };

  const handleOnChange = event => {
    console.log('username is now', event.target.value);
    username = event.target.value;
  };
  
  useEffect(() => {
    getStatus().then(function(results){
      // access results here by chaining to the returned promise
      const res = results.processing;
      const usr = results.username;
      const updatetime = results.imgDateTime;
      const ql = results.queuelen;
      setStatus(res);
      setUsername(usr);
      setUpdateTime(updatetime);
      setQueueLen(ql);
    }); //getStatus.request username && reqNum && pic update date
  }, []);

  return (
    <main className={classes.content}>

        <SimpleSnackbar handleClose={handleClose} open={open} message={popMessage} />

        <div className={classes.toolbar} />
        <Typography paragraph>
          This is a real-time picture of a tray of balls. <br/>You have the power to tilt the tray.<br/>
          {/*<br /><br />*/}
          There are {status == 'true' ? {queuelen} : "no"} requests on the queue and the server is currently {status == 'true' ? "processing a request by " + {curUsername} : "resting"}.
        </Typography>
        <img src="http://120.78.126.174:3000/ladylib.jpg" className="App-logo" alt="logo" />
        <Typography variant="caption" display="block" gutterBottom>
          Updated {updateTime} EST <br />
        </Typography>

        <form className={classes.inputText} noValidate autoComplete="off">
          <TextField id="standard-basic" inputProps={{maxLength: 35}} label="Name" placeholder={username} onChange={handleOnChange}/>
        </form>

        <Button variant="contained" color="primary" size="large" onClick={() => postPumpRequest(username, 'left', handleClick)}>
          TILT LEFT
        </Button>&nbsp;
        <Button variant="contained" color="primary" size="large" onClick={() => postPumpRequest(username, 'center', handleClick)}>
          TILT CENTER
        </Button>&nbsp;
        <Button variant="contained" color="primary" size="large" onClick={() => postPumpRequest(username, 'right', handleClick)}>
          TILT RIGHT
        </Button>
      </main>
  );
}

